import React, { useState } from 'react';

const ContactForm = (props) =>  {
  const { onSubmit } = props;

  const [fullName, setFullName] = useState('');
  const [phone, setPhone] = useState('');
  const [email, setEmail] = useState('');

  return <form onSubmit={(e) => onSubmit(e, fullName, phone, email)}>
    <input id="fullName" type='text' placeholder='Full Name' onChange={e => setFullName(e.target.value)}></input>
    <input id="phone" type='phone' placeholder='8888888888' onChange={e => setPhone(e.target.value)}></input>
    <input id="email" type='email' placeholder='placeholder@email.com' onChange={e => setEmail(e.target.value)}></input>
    <input type='submit' name='submit' />
  </form>;
}

export default ContactForm;
